import { useEffect, useRef, useState } from "react";
import "./scanQR.css";
import { useNavigate } from "react-router-dom";
import jsQR from "jsqr";
import Icon_back from '../assets/icon_back.png';
import Qr from '../assets/qr.png';

export default function ScanQR() {
  const navigate = useNavigate();
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const [isDetected, setIsDetected] = useState(false); // QR   

  useEffect(() => {
    let animationFrameId;

    const startCamera = async () => {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: "environment" }
        });
        if (videoRef.current) {
          videoRef.current.srcObject = stream;
          //      
          animationFrameId = requestAnimationFrame(tick);
        }
      } catch (err) {
        console.error("  :", err);
      }
    };

    const tick = () => {
      if (videoRef.current && videoRef.current.readyState === videoRef.current.HAVE_ENOUGH_DATA) {
        const video = videoRef.current;
        const canvas = canvasRef.current;
        const context = canvas.getContext("2d", { willReadFrequently: true });

        canvas.height = video.videoHeight;
        canvas.width = video.videoWidth;
        
        //    
        context.drawImage(video, 0, 0, canvas.width, canvas.height);
        
        //     QR 
        const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
        const code = jsQR(imageData.data, imageData.width, imageData.height, {
          inversionAttempts: "dontInvert",
        });

        if (code) {
          // QR   
          console.log("Found QR code", code.data);
          setIsDetected(true);
        } else {
          setIsDetected(false);
        }
      }
      animationFrameId = requestAnimationFrame(tick);
    };

    startCamera();

    return () => {
      cancelAnimationFrame(animationFrameId);
      if (videoRef.current && videoRef.current.srcObject) {
        videoRef.current.srcObject.getTracks().forEach(track => track.stop());
      }
    };
  }, []);

  const handleCheckIn = () => {
    if (isDetected) {
      alert(" !");
      navigate("/home");
    } else {
      alert("QR   .   .");
    }
  };

  return (
    <div className="qr-page">
      <header className="header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <img src={Icon_back} alt="" />
        </button>
        <h1> </h1>
      </header>

      <video ref={videoRef} playsInline autoPlay style={{ display: "none" }} />
      
      {/*    (  ) */}
      <canvas ref={canvasRef} className="camera-background" />

      <div className="qr-container">
        <div className="scan-frame">
          <div className="corner corner-tl"></div>
          <div className="corner corner-tr"></div>
          <div className="corner corner-bl"></div>
          <div className="corner corner-br"></div>
          <div className="scan-line"></div>
        </div>
        <div className="floatingScanner">
          <div className="scannerIcon" style={{ backgroundImage: `url(${Qr})` }} onClick={handleCheckIn} />
        </div>
      </div>
    </div>
  );
}