import React, { useEffect, useState } from 'react';
import SuggestionCard from './SuggestionCard';
import './SuggestionPage.css';
import { useNavigate } from "react-router-dom";
import Icon_edit from '../assets/icon_edit.png';
import Icon_back from '../assets/icon_back.png';
import { supabase } from "../supabaseClient";

//    
export function getTimeAgo(dateString) {
  const now = new Date();
  const past = new Date(dateString);
  const diff = Math.floor((now - past) / 1000); // 

  if (diff < 60) return `${diff} `;
  if (diff < 3600) return `${Math.floor(diff / 60)} `;
  if (diff < 86400) return `${Math.floor(diff / 3600)} `;
  return `${Math.floor(diff / 86400)} `;
}

export default function SuggestionPage() {
  const navigate = useNavigate();
  const [suggestions, setSuggestions] = useState([]);
  const [userId, setUserId] = useState(null);
  const [avatarUrl, setAvatarUrl] = useState('/logo192.png'); //  

  useEffect(() => {
    const loadUser = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      if (user) {
        setUserId(user.id);
        //   
        const avatar = user.user_metadata?.avatar_url || user.user_metadata?.picture;
        if (avatar) setAvatarUrl(avatar);
      }
    };
    loadUser();
  }, []);

  useEffect(() => {
    if (!userId) return;
    const fetchSuggestions = async () => {
      const { data, error } = await supabase
      .from('suggestions')
      .select(`
        id,
        content,
        created_at,
        user_name,
        profiles (
          roomNum,
          avatar_url
        )
      `)
      .eq('user_id', userId)
      .order('created_at', { ascending: false });


      if (error) console.error(" :", error);
      else setSuggestions(data);
    };
    fetchSuggestions();
  }, [userId]);

  const handleDelete = async (id) => {
    const { error } = await supabase.from('suggestions').delete().eq('id', id);
    if (error) return alert(" ");
    setSuggestions(prev => prev.filter(s => s.id !== id));
  };

  return (
    <div className="suggestion-page">
      <div className="header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <img src={Icon_back} alt="" />
        </button>
        <h1></h1>
        <button className="edit-btn" onClick={() => navigate("/suggestion/write")}>
          <img src={Icon_edit} alt="" />
        </button>
      </div>

      <div className="suggestion-list">
        {suggestions.length === 0 && <p>  .</p>}
        {suggestions.map(s => (
        <SuggestionCard
          key={s.id}
          profile={s.profiles?.avatar_url}
          name={`${s.profiles?.roomNum ?? ''} ${s.user_name}`}
          timeAgo={getTimeAgo(s.created_at)}
          content={s.content}
          onDelete={() => handleDelete(s.id)}
        />

      ))}

      </div>
    </div>
  );
}
