import React, { useState, useEffect, useMemo, useRef } from 'react';
import { useNavigate } from "react-router-dom";
import { supabase } from "../supabaseClient";
import Back from '../assets/back.png';
import Profile from '../assets/profile.png';
import Camera from '../assets/camera.png';
import Drop from '../assets/drop.png';
import './EditInformation.css';

const allRooms = {
  '3': [
    '301','302','303','304','305','306',
    '307','308','309','310','311','312',
    '313','314','315','316','317','318'
  ],
  '4': [
    '401','402','403','404','405','406',
    '407','408','410','411','412','413',
    '414','415','416','417','418'
  ],
  '5': [
    '501','502','503','504','505','506',
    '507','508','509','510','511','512',
    '513','514','515','516','517','518'
  ],
};

export default function EditInformation() {
  const navigate = useNavigate();
  const fileInputRef = useRef(null);

  const [userId, setUserId] = useState(null);
  const [nameOnly, setNameOnly] = useState("");
  const [profileImage, setProfileImage] = useState(null);
  const [selectedFloor, setSelectedFloor] = useState('4');
  const [selectedRoom, setSelectedRoom] = useState(allRooms['4'][0]);
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  const currentRooms = useMemo(
    () => allRooms[selectedFloor],
    [selectedFloor]
  );

/* =====  +   ===== */
useEffect(() => {
  const loadData = async () => {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) return;

    setUserId(user.id);

    // =====  =====
    const fullName = user.user_metadata?.full_name ?? "";
    setNameOnly(fullName.includes("_") ? fullName.split("_")[1] : fullName);

    // =====     =====
    const googleAvatar =
      user.user_metadata?.picture ||
      user.identities?.[0]?.identity_data?.picture ||
      null;

    // ===== profiles  =====
    const { data: profile } = await supabase
      .from("profiles")
      .select("roomNum, avatar_url")
      .eq("user_id", user.id)
      .single();

    if (profile?.roomNum) {
      const floor = profile.roomNum[0] + "";
      setSelectedFloor(floor);
      setSelectedRoom(profile.roomNum + "");
    }

    //   
    setProfileImage(profile?.avatar_url || googleAvatar);
  };

  loadData();
}, []);


  /* =====    ===== */
  const handleImageUpload = async (e) => {
    const file = e.target.files[0];
    if (!file || !userId) return;

    const ext = file.name.split('.').pop();
    const filePath = `${userId}.${ext}`;

    const { error: uploadError } = await supabase.storage
      .from("avatars")
      .upload(filePath, file, { upsert: true });

    if (uploadError) {
      console.error(uploadError);
      alert("  ");
      return;
    }

    const { data } = supabase.storage
      .from("avatars")
      .getPublicUrl(filePath);

    const publicUrl = data.publicUrl;

    await supabase
      .from("profiles")
      .update({ avatar_url: publicUrl })
      .eq("user_id", userId);

    setProfileImage(publicUrl);
  };

  /* =====   ===== */
  const handleSave = async () => {
    if (!userId) return;

    await supabase
      .from("profiles")
      .update({
        roomNum: selectedRoom.replace("", "")
      })
      .eq("user_id", userId);

    navigate(-1);
  };

  return (
    <div className="EditInformationContainer">
      <header className="MypageHeader">
        <div
          className="backButton"
          onClick={() => navigate(-1)}
          style={{ backgroundImage: `url(${Back})` }}
        />
        <h1 className="EditpageTitle"> </h1>
        <div className="null" />
      </header>

      <div className="EditprofileSection">
        <div className="EditprofileImageWrapper">
          <img
            src={profileImage ?? Profile}
            alt="profile"
            className="EditprofileImage"
            referrerPolicy="no-referrer"
          />

          <img
            src={Camera}
            alt="edit"
            className="editIcon"
            onClick={() => fileInputRef.current.click()}
          />
          <input
            type="file"
            accept="image/*"
            ref={fileInputRef}
            style={{ display: "none" }}
            onChange={handleImageUpload}
          />
        </div>
      </div>

      <div className="EditinfoSection">
        <p className="EditinfoLabel"></p>
        <input
          type="text"
          value={nameOnly}
          disabled
          className="EditinfoBox"
        />
      </div>

      <div className="Editroom-selector-container">
        <span className="EditinfoLabel"></span>

        <div className="Editfloor-toggle-group">
          {Object.keys(allRooms).map((floor) => (
            <button
              key={floor}
              className={`Editfloor-toggle-button ${
                selectedFloor === floor ? 'selected' : ''
              }`}
              onClick={() => {
                setSelectedFloor(floor);
                setSelectedRoom(allRooms[floor][0]);
              }}
            >
              {floor}
            </button>
          ))}
        </div>

        <div className="Editroom-dropdown-container">
          <div
            className="Editroom-display-box"
            onClick={() => setIsDropdownOpen(!isDropdownOpen)}
          >
            {selectedRoom}
            <img
              src={Drop}
              alt="arrow"
              className={`Editdropdown-arrow ${isDropdownOpen ? 'Editopen' : ''}`}
            />
          </div>

          {isDropdownOpen && (
            <ul className="Editroom-options-list">
              {currentRooms.map((room) => (
                <li
                  key={room}
                  className={`Editroom-option-item ${
                    selectedRoom === room ? 'Edithighlighted' : ''
                  }`}
                  onClick={() => {
                    setSelectedRoom(room);
                    setIsDropdownOpen(false);
                  }}
                >
                  {room}
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>

      <button className="EditsaveButton" onClick={handleSave}>
        
      </button>
    </div>
  );
}
