import React, { useState, useEffect } from "react";
import { supabase } from "../supabaseClient";
import { useNavigate } from "react-router-dom";
import "./SelectRoom.css";

export default function SelectRoom() {
  const navigate = useNavigate();
  const [selectedFloor, setSelectedFloor] = useState(4);
  const [selectedRoom, setSelectedRoom] = useState("");
  const [name, setName] = useState("");
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  //   
  useEffect(() => {
    const getUser = async () => {
      const {
        data: { session },
      } = await supabase.auth.getSession();

      if (!session) return;

      const user = session.user;
      const fullName = user.user_metadata?.full_name ?? "";
      const parsedName = fullName.includes("_")
        ? fullName.split("_")[1]
        : fullName;

      setName(parsedName);
    };

    getUser();
  }, []);

  //      (  )
  useEffect(() => {
    const handleClickOutside = (e) => {
      if (isDropdownOpen && !e.target.closest(".dropdownContainer")) {
        setIsDropdownOpen(false);
      }
    };

    document.addEventListener("click", handleClickOutside);
    return () => document.removeEventListener("click", handleClickOutside);
  }, [isDropdownOpen]);

  const getRoomsForFloor = (floor) => {
    const start = floor * 100 + 1;
    return Array.from({ length: 18 }, (_, i) => start + i);
  };

  const rooms = getRoomsForFloor(selectedFloor);

  //   main 
  const handleSelectRoom = async () => {
    if (!selectedRoom) {
      alert(" ");
      return;
    }

    const {
      data: { session },
    } = await supabase.auth.getSession();

    if (!session) {
      alert(" ");
      navigate("/login");
      return;
    }

    const user = session.user;

    const { error } = await supabase
      .from("profiles")
      .upsert(
        {
          user_id: user.id,
          name: name,
          roomNum: selectedRoom,
        },
        { onConflict: "user_id" }
      );

    if (error) {
      console.log(" :", error.message);
      alert(" ");
    } else {
      navigate("/home");
    }
  };

  return (
    <div className="selectRoomContainer">
      <h1>
         
        <br />
         
      </h1>

      <div className="floorTabs">
        {[3, 4, 5].map((floor) => (
          <button
            key={floor}
            className={`floorTab ${
              selectedFloor === floor ? "active" : ""
            }`}
            onClick={() => {
              setSelectedFloor(floor);
              setSelectedRoom("");
            }}
          >
            {floor}
          </button>
        ))}
      </div>

      <div className="dropdownContainer">
        <div
          className="dropdownHeader"
          onClick={(e) => {
            e.stopPropagation();
            setIsDropdownOpen(!isDropdownOpen);
          }}
        >
          <span>
            {selectedRoom
              ? `${selectedRoom}`
              : "  ."}
          </span>
          <span className="dropdownArrow">
            <img src="arrow.svg" alt="" width="12" height="12" />
          </span>
        </div>

        {isDropdownOpen && (
          <div className="dropdownList">
            {rooms.map((room) => (
              <div
                key={room}
                className={`dropdownItem ${
                  selectedRoom === room ? "selected" : ""
                }`}
                onClick={(e) => {
                  e.stopPropagation();
                  setSelectedRoom(room);
                  setIsDropdownOpen(false);
                }}
              >
                {room}
              </div>
            ))}
          </div>
        )}
      </div>

      <button onClick={handleSelectRoom} className="saveButton">
        
      </button>

      {/*  
      <button
        onClick={async () => {
          await supabase.auth.signOut();
          console.log(" ");
          navigate("/login");
        }}
      >
        
      </button> */}
    </div>
  );
}
