import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import './SuggestionWritePage.css';
import Icon_Back from '../assets/icon_back.png';
import Icon_Image from '../assets/icon_image.png';
import { supabase } from "../supabaseClient";

export default function SuggestionWritePage() {
  const navigate = useNavigate();
  const [content, setContent] = useState("");
  const [imageFile, setImageFile] = useState(null);
  

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    setImageFile(file); // 1
  };


  const handleRemoveImage = () => {
  setImageFile(null);
};


  const handleRegister = async () => {
  //    
  const { data: { user }, error } = await supabase.auth.getUser();
  if (error) {
    console.log("   ", error);
    return;
  }
  if (!user) {
    console.log(" ");
    return;
  }

  //   
  const { data: profileData, error: profileError } = await supabase
    .from('profiles')
    .select('name, roomNum')
    .eq('user_id', user.id)
    .single();

  if (profileError) {
    console.log("   ", profileError);
    return;
  }

  let imagePath = null;

  if (imageFile) {
    const fileExt = imageFile.name.split('.').pop();
    const fileName = `${user.id}_${Date.now()}.${fileExt}`;

    const { error: uploadError } = await supabase.storage
      .from('suggestion_image')
      .upload(fileName, imageFile);

    if (uploadError) {
      console.error("  ", uploadError);
      return;
    }

    imagePath = fileName; // DB  path
  }


  // suggestions  
  const { data, error: insertError } = await supabase
  .from('suggestions')
  .insert([
    {
      user_id: user.id,
      user_name: profileData.name,
      roomNum: profileData.roomNum,
      content: content,
      image_path: imagePath,
      created_at: new Date().toISOString(),
    }
  ]);


  if (insertError) {
    console.error(" :", insertError);
    return;
  }

  console.log(" :", data);
  navigate(-1); //  
};



  return (
    <div className="suggestion-write-page">
      {/*   */}
      <div className="header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <img src={Icon_Back} alt="" />
        </button>
        <h1> </h1>
        <button className="register-btn" onClick={handleRegister}>
          
        </button>
      </div>

      <div className="divider"></div>

      {/*   */}
      <textarea
        className="suggestion-input"
        placeholder=" ..."
        value={content}
        onChange={(e) => setContent(e.target.value)}
      />

      <div className="divider divider-bottom"></div>

      {/*   +   */}
      <div className="bottom-bar">
        <label className="photo-btn">
          <img src={Icon_Image} alt=" " />
          <input
            type="file"
            accept="image/*"
            style={{ display: "none" }}
            onChange={handleImageChange}
          />
        </label>

        {imageFile && (
          <div className="image-info">
            <span className="file-name">{imageFile.name}</span>
            <button className="remove-image-btn" onClick={handleRemoveImage}>
              
            </button>
          </div>
        )}
      </div>

    </div>
  );
}
