import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import styled from "styled-components";
import Icon_back from '../assets/icon_back.png';
import noticeData from "../data/notice.json"; // JSON 
import {
  Container, Header, BackBtn, Title, ScrollArea, ListBox, NoticeItem,
  ItemRow, NoticeTitle, NoticeDate
} from "./noticeList.styles";

/*export const Container = styled.div`
  width: 393px;
  height: 898px;
  background: #ffffff;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
`;
*/


export default function NoticeList() {
  const navigate = useNavigate();
  const [noticeList, setNoticeList] = useState([]);

  const sortedNotices = [...noticeData].sort(
    (a, b) => new Date(b.date) - new Date(a.date) //  
  );

  useEffect(() => {
    // JSON  
    setNoticeList(noticeData);
  }, []);

  const handleClick = (id) => {
    //    
    navigate(`/notices/${id}`);
  };

  return (
    <Container>
      <Header>
        <BackBtn onClick={() => navigate(-1)}>
          <img src={Icon_back} alt="" />
        </BackBtn>
        <Title></Title>
      </Header>
      <ScrollArea>
      <ListBox>

      {sortedNotices.map((n) => (
        <NoticeItem key={n.id} onClick={() => handleClick(n.id)}>
          <NoticeTitle>{n.title}</NoticeTitle>
          <NoticeDate>{n.date}</NoticeDate>
        </NoticeItem>
      ))}
      </ListBox>
      </ScrollArea>
    </Container>
  );
}