import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import Back from '../assets/back.png';
import { supabase } from "../supabaseClient";
import './Song.css';


async function fetchAlbumArt(title, artist) {
  const query = encodeURIComponent(`${artist} ${title}`);
  const url = `https://itunes.apple.com/search?term=${query}&entity=song&limit=1`;

  try {
      const response = await fetch(url);
      const data = await response.json();

      if (data.results.length > 0) {
          return data.results[0].artworkUrl100.replace("100x100", "300x300");
      }
      return null;
  } catch (error) {
      console.error("iTunes API :", error);
      return null;
  }
}

function Song(){
  const [songTitle, setSongTitle] = useState('');
  const [songArtist, setSongArtist] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async () => {
    if (!songTitle || !songArtist) {
      alert("   !");
      return;
    }

    // 1.  
    const { data: existing, error: checkError } = await supabase
      .from('songs')
      .select('*')
      .eq('title', songTitle)
      .eq('artist', songArtist)
      .limit(1);

    if (checkError) {
      console.error("  :", checkError);
      alert(" .");
      return;
    }

    if (existing.length > 0) {
      alert("  !");
      return;
    }

    // 2. iTunes   
    const albumImg = await fetchAlbumArt(songTitle, songArtist);

    // 3. DB 
    const { error } = await supabase
      .from('songs')
      .insert([
        {
          title: songTitle,
          artist: songArtist,
          img_url: albumImg || "https://placehold.co/300x300?text=No+Image"
        }
      ]);

    if (error) {
      console.error(" :", error);
      alert(" ");
      return;
    }

    // 4.   (state )
    navigate("/home", {
      state: {
        newSong: {
          title: songTitle,
          artist: songArtist,
          imgUrl: albumImg || "https://placehold.co/300x300?text=No+Image"
        }
      }
    });
  };

  return (
    <div className="screenContainer">
      <header className="header">
        <div
          className="backButton"
          onClick={() => navigate(-1)}
          style={{ backgroundImage: `url(${Back})` }}
        />
        <h1 className="pageTitle"> </h1>
        <div className="placeholder" style={{ width: 24, height: 24 }} />
      </header>

      <main className="contentArea">
        <div className="inputGroup">
          <p className="inputLabel1"> </p>
          <p className="inputLabel2">    </p>
          <input
            className="textInput"
            type="text"
            placeholder=" "
            value={songTitle}
            onChange={(e) => setSongTitle(e.target.value)}
          />
        </div>

        <div className="inputGroup">
          <p className="inputLabel1"> </p>
          <p className="inputLabel2">    </p>
          <input
            className="textInput"
            type="text"
            placeholder=" "
            value={songArtist}
            onChange={(e) => setSongArtist(e.target.value)}
          />
        </div>
      </main>

      <footer className="footer">
        <button className="submitButton" onClick={handleSubmit}>
          
        </button>
      </footer>
    </div>
  );
};

export default Song;
